const tools = require('../utils/tools'); // 
const applications = require('../utils/applications'); // 
const Super = require('../components-dom-super/g-super'); // ()
// ()
const route = '/phone/';

// 
const Sub = tools.constructorInherit(Super, {
    // 
    callback: {},
    // 
    config: {},
    // 
    data: {
        items: [
            {
                href: route,
                icon: 'icon-home',
                text: '',
                isShowMark: false,
            },
            {
                href: `${route}dev-globals/`,
                icon: 'icon-dev',
                text: '',
                isShowMark: false,
            },
            {
                href: `${route}dev-components/`,
                icon: 'icon-dev',
                text: '',
                isShowMark: false,
            },
            {
                href: `${route}dev-words/`,
                icon: 'icon-dev',
                text: '',
                isShowMark: false,
            },
            {
                href: `${route}mine/`,
                icon: 'icon-mine',
                text: '',
                isShowMark: false,
            },
        ],
    },
});

// ()
Sub.prototype.moduleDomCreate = function () {
    const data = this.opts.data;
    const items = data.items;
    let html = '';
    items.forEach(function (v) {
        let markHtml = '';
        if (v.isShowMark) {
            markHtml = '<div class="g-navigation-mark"></div>';
        }
        html += `
            <a href="${v.href}" class="g-navigation-item">
                <div class="g-navigation-icon iconfont ${v.icon}"></div>
                <div class="g-navigation-text">${v.text}</div>
                ${markHtml}
            </a>
        `;
    });
    this.moduleDom = applications.createElement({
        style: this.opts.config.moduleDomStyle,
        customAttribute: this.opts.config.moduleDomCustomAttribute,
        attribute: {
            className: 'g-navigation',
            innerHTML: html,
        },
    });
};

// ()
Sub.prototype.power = function () {
    // ...
};

module.exports = Sub;



